import {
	mapMutations
} from 'vuex';
import api from '@/common/index.js';
export default {
	data() {
		return {
			version: '',
			hdid: false
		};
	},
	onLoad(){
		api.pageTitle(this.$t('set').title)
	},
	computed:{
		i18n(){
			return this.$t('set');
		}
	},
	onShow() {













	},
	methods: {
		...mapMutations(['logout']),
		allclose() {
			uni.clearStorage();
			this.$api.msg(this.$t('set').hint)
		},
		//退出登录
		toLogout() {
			let that = this;
			uni.showModal({
				content: that.$t('set').modelMsg,
				cancelText:that.$t('index').cancel,
				confirmText:that.$t('index').confirm,
				success: (e) => {
					if (e.confirm) {
						this.logout();
						uni.closeSocket();
						setTimeout(function(){
							uni.reLaunch({
								url: '/pages/login/login'
							})
						}, 500)
					}
				}
			});
		},
		udate() {






























		},
	}

}
